<template>
  <div class="portfolio">
    <div class="portfolio-block">
      <a
        v-for="(item, index) in this.projects"
        :key="index"
        @click="updatePortfolioItem(index)"
        v-bind:class="currentIndex === index ? 'active' : ''"
      >
        <img :src="item.project_logo" alt>
      </a>
    </div>
    <div class="work">
      <transition name="fade" mode="out-in">
        <div class="container" v-if="show">
          <div class="header">
            <h2>{{ currentProject.project }}</h2>
          </div>
          <div class="project">
            <div class="project__about">
              <p>{{ currentProject.project_brief }}</p>
              <div class="built-with">Built With</div>
              <ul class="technology">
                <li v-for="(item, index) in currentProject.technology" :key="index">
                  <i :class="item.icon"></i>
                </li>
              </ul>
              <a
                v-if="currentProject.project_link"
                class="btn"
                :href="currentProject.project_link"
              >Visit Site</a>
            </div>

            <carousel
              :per-page="1"
              class="project__images"
              paginationActiveColor="orangered"
              paginationPosition="bottom-overlay"
            >
              <slide v-for="(slide, index) in currentProject.images" :key="index">
                <img :src="slide.url" :alt="slide.alt" style="display: block">
              </slide>
            </carousel>
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
const axios = require("axios");
import { Carousel, Slide } from "vue-carousel";

export default {
    name: "Profolio",
    components: {
        Carousel,
        Slide
    },
    data() {
        return {
            projects: {},
            currentProject: {},
            currentIndex: 0,
            show: true
        };
    },
    methods: {
        updatePortfolioItem(index = 0) {
            this.show = false;
            setTimeout(() => {
                this.currentIndex = index;
                this.currentProject = this.projects[index];
                this.show = true;
            }, 500);
        }
    },
    mounted() {
        axios.get("./project-data.json").then(response => {
            this.projects = response.data;
            this.updatePortfolioItem();
        });
    }
};
</script>

